import React from "react";
import "./ssh.style.css";
import SSH_DATA from "../sshData.js";
import s1 from "../Screenshoots/s1.png";
import s_1 from "../Screenshoots/s1-.png";
import s2 from "../Screenshoots/s2.png";
import s3 from "../Screenshoots/s3.png";
import s4 from "../Screenshoots/s4.png";
import s5 from "../Screenshoots/s5.png";
import s6 from "../Screenshoots/s6.png";
import s7 from "../Screenshoots/s7.png";
import s9 from "../Screenshoots/s9.png";
import s10 from "../Screenshoots/s10.png";

const SSH1 = () => (
  <div className='torrent-tips'>
    <div className='torrent-tips__step-1'>
      <h2>{SSH_DATA["step2"]["title"]}</h2>
      <p className='step-1_discription'>{SSH_DATA["step2"]["description"]}</p>
    </div>
    <div className='screenshots screenshots__ssh'>
      <img className='screenshots__pic screenshots__small' src={s1} alt='' />
      <img className='screenshots__pic screenshots__small' src={s_1} alt='' />
      <img className='screenshots__pic screenshots__small' src={s2} alt='' />
      <img className='screenshots__pic screenshots__small' src={s3} alt='' />
      <img className='screenshots__pic screenshots__small' src={s4} alt='' />
      <img className='screenshots__pic screenshots__small' src={s5} alt='' />
      <img className='screenshots__pic screenshots__small' src={s6} alt='' />
      <img className='screenshots__pic screenshots__small' src={s7} alt='' />
      <img className='screenshots__pic screenshots__small' src={s9} alt='' />
      <img className='screenshots__pic screenshots__small' src={s10} alt='' />
    </div>
  </div>
);
export default SSH1;
