import React from "react";
import "./ssh.style.css";
import SSH_DATA from "../sshData";
import ssh2 from "../Screenshoots/ssh2.PNG";
import ssh3 from "../Screenshoots/ssh3.PNG";
const SSH = () => (
  <div className='torrent-tips'>
    <div className='torrent-tips__step-1'>
      <h2>{SSH_DATA["step1"]["title"]}</h2>
      <p className='step-1_discription'>{SSH_DATA["step1"]["description"]}</p>
    </div>
    <div className='popular_sites'>
      <ul>
        {SSH_DATA["step1"]["sites"].map(({ id, name, url }) => (
          <li key={id}>
            <a className='popular_sites__name' href={url}>
              {name}
            </a>
          </li>
        ))}
      </ul>
    </div>
    <div className='screenshots'>
      <img className='screenshots__pic' src={ssh2} alt='' />
      <img className='screenshots__pic' src={ssh3} alt='' />
    </div>
  </div>
);
export default SSH;
