import React from "react";
import T_DATA from "../torrentData";
import "./torrent.style.css";
import pic0 from "../Screensshot/0.PNG";
import pic1 from "../Screensshot/1.PNG";
import pic2 from "../Screensshot/2.PNG";
import pic4 from "../Screensshot/4.PNG";
import pic5 from "../Screensshot/5.PNG";
const Torrent2 = () => (
  <div className='torrent-tips'>
    <div className='torrent-tips__step-1'>
      <h2>{T_DATA["step2"]["title"]}</h2>
      <p className='step-1_discription'>{T_DATA["step2"]["description"]}</p>
    </div>
    <div className='popular_sites'>
      <ul>
        {T_DATA["step2"]["sites"].map(({ id, name, url }) => (
          <li key={id}>
            <a className='popular_sites__name' href={url}>
              {name}
            </a>
          </li>
        ))}
      </ul>
    </div>
    <div className='screenshots'>
      <img className='screenshots__pic' src={pic0} alt='' />
      <img className='screenshots__pic' src={pic1} alt='' />
      <img className='screenshots__pic' src={pic4} alt='' />
      <img className='screenshots__pic' src={pic2} alt='' />
      <img className='screenshots__pic' src={pic5} alt='' />
    </div>
  </div>
);
export default Torrent2;
