import React from "react";
import T_DATA from "../torrentData";
import "./torrent.style.css";
const Torrent1 = () => (
  <div className='torrent-tips'>
    <div className='torrent-tips__step-1'>
      <h2>{T_DATA["step1"]["title"]}</h2>
      <p className='step-1_discription'>{T_DATA["step1"]["description"]}</p>
    </div>
    <div className='popular_sites'>
      <ul>
        {T_DATA["step1"]["sites"].map(({ id, name, url }) => (
          <li key={id}>
            <a className='popular_sites__name' href={url}>
              {name}
            </a>
          </li>
        ))}
      </ul>
    </div>
  </div>
);
export default Torrent1;
